/* Components */
.header {
  position: sticky;
  top: 0;
  z-index: 2;
}

.header-items {
  background: #f3e9d4;
  padding: 1.429em 1.357em 0 1.357em;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-logo {
  display: block;
  background: url("../img/svg/logo.svg") no-repeat;
  width: 10.7em;
  height: 3.5em;
  overflow: hidden;
  text-indent: -3000px;
  background-size: 100%;
}

.header-controls {
  display: flex;
  align-items: center;
}

.header-controls a {
  display: inherit;
  margin-right: 1.6em;
}

.header-menu-button {
  cursor: pointer;
  right: 1.357em;
  width: 1.6em;
  height: 1.6em;
  background: url("../img/svg/burger.svg") no-repeat;
  background-size: cover;
}

.header .button-phone {
  right: 4.5em;
}

.header-title {
  position: relative;
  z-index: 1;
  color: #fff;
  text-align: center;
  background: #23647f;
  padding: 0.525em 0.3em;
}

.header-title a {
  color: #fff;
  display: inline-block;
}

.header-image {
  text-align: center;
  color: #fff;
  font-family: "helveticaneuecyrultralight";
  padding: 3.5em 0 0 0;
  position: relative;
}

.header-image-container {
  font-size: 3em;
  padding-bottom: 1.3em;
}

.header-image-title {
  font-size: 2em;
}

.header__wrap-img {
  position: relative;
  background: url("/img/background/main.jpeg") no-repeat 50% 0;
  background-size: cover;
  height: 36em;
}

.header__wrap-img .header {
  background: none;
}

.header__wrap-img .header-title {
  background: rgba(0, 0, 0, 0.4);
}

.header__wrap-img:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: rgba(0, 0, 0, 0.7);
}

.header__wrap-img.university {
  height: 19em;
}

.header__wrap-img.university:before {
  background-color: rgba(0, 0, 0, 0.4);
}

.header-main .header-logo {
  background-image: url("../img/svg/logo-white.svg");
}

.header-main .header-menu-button {
  background-image: url("../img/svg/burger-white.svg");
}

.header-main .header-image {
  text-align: left;
  padding: 1.429em 1.357em 1.429em 1.357em;
}

.header-main .header-image p {
  font-family: "helveticaneuecyrlight" !important;
}

.header-main .header-image-container {
  line-height: 48px;
  padding-bottom: 20px;
}

.footer {
  background: #3b3b3b;
  color: #fff;
  text-align: left;
  font-family: 'helveticaneuecyrlight';
  font-weight: normal;
  width: 100%;
  padding: 2em 0 1.45em;
  box-sizing: content-box;
  margin-top: 2em;
  text-align: center;
}

.footer-message {
  padding: 0 0 0.625em 0;
}

.footer-title {
  color: #b6b6b6;
  font-size: 1.250em;
  font-family: 'helveticaneuecyrmedium';
  padding: 0 0 1.763em 0;
}

.footer a {
  color: #fff;
}

.footer-menu {
  margin: 0;
  padding: 0 0 1.70em 0;
  list-style: none;
}

.footer-menu:last-of-type {
  padding: 0;
}

.footer-menu li {
  padding: 0 0 0.725em 0;
}

.footer .button-green-large, .footer .button-green-small, .footer .button-blue-large, .footer .button-white-large {
  margin: 0 0 1.750em 0;
}

.footer.hidden {
  display: block !important;
  margin-top: 2em;
}

.footer-icon {
  margin-bottom: 30px;
}

.footer-icon img {
  height: 38px;
  margin-bottom: 6px;
}

.tutors {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tutors .list-main {
  padding: 0;
}

.tutors .list-main li:last-of-type {
  padding: 0;
}

.tutors-item {
  padding: 0 0 1em 0;
}

.tutors-item:after {
  content: '';
  display: block;
  clear: both;
  height: 1px;
  line-height: 0;
}

.tutors-item-img {
  float: left;
  width: 30%;
  max-width: 9.375em;
}

.tutors-item-img img {
  width: 100%;
  border-radius: 4px;
}

.tutors-item-right {
  box-sizing: border-box;
  float: left;
  width: 70%;
  padding: 0 0 0 1em;
}

.tutors-item-title {
  font-family: 'helveticaneuecyrmedium';
  padding: 0 0 1em 0;
}

.tutors-item-description {
  padding: 1em 0;
}

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  box-sizing: border-box;
  padding: 0.938em;
}

.popup-body {
  overflow: hidden;
}

.popup-close {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.popup-main {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  margin: 0 0 0 -7px;
  z-index: 2;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #ffffff;
  border-radius: 4px;
}

.popup-main-wrap {
  overflow-y: scroll;
  height: 100%;
  position: relative;
  z-index: 1000;
}

.popup-main-close-button {
  background: #dcdcdc;
  border-radius: 4px 4px 0 0;
  height: 2em;
  padding: 0.35em 0 0 0;
  cursor: pointer;
  box-sizing: border-box;
}

.popup-main-close-button:after {
  content: '';
  display: inline-block;
  width: 1.3em;
  height: 1.3em;
  background: url("../img/icons/cross-out.svg") no-repeat;
  background-size: cover;
}

.popup.active {
  display: block !important;
}

.popup:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 1px;
  height: 100%;
}

.licence-list {
  padding: 0 0 30px 0;
  margin: 0;
  list-style: none;
}

.main-menu {
  padding: 1.6em 1.2em;
}

.main-menu-list {
  margin: 0;
  padding: 0 0 1.250em 0;
  list-style: none;
}

.main-menu-list-item {
  padding: 0 0 1em 0;
}

.top-message {
  position: relative;
  background: #fef9de;
  border-bottom: 0.063em solid #ffe65c;
  padding: 0.5em 0.3em;
  color: #af0000;
  text-align: center;
}

.address-list {
  margin: 0 -1.357em 0 -1.357em;
  padding: 0 0 2.500em 0;
  list-style: none;
  border-top: 0.063em solid #dcdcdc;
}

.address-list-item {
  padding: 0.8em 1.357em 0.8em 2.714em;
  position: relative;
  border-bottom: 0.063em solid #dcdcdc;
}

.address-list-item > a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
}

.address-list-item > a > img {
  height: 0.75em;
  width: 0.75em;
}

.address-list-item > a .address-branch-title {
  text-decoration: underline;
}

.address-list-item-info {
  float: right;
  color: #818181;
  text-align: right;
  line-height: 1.550em;
  width: 12.295em;
  box-sizing: border-box;
  padding-right: 1.357em;
  position: relative;
}

.address-list-item-more {
  color: #818181;
  padding-top: 0.438em;
}

.address-list-metro:before {
  content: '';
  width: 0.500em;
  height: 0.500em;
  border-radius: 100%;
  position: absolute;
  top: 1.15em;
  left: 1.357em;
}

.address-list-metro__orange:before {
  background: #de823a;
}

.address-list-metro__red:before {
  background: #9e2a47;
}

.address-list-metro__green:before {
  background: #35a45b;
}

.address-list-metro__grey:before {
  background: #c4c4c4;
}

.address-list-metro__greenlight:before {
  background: #c0dc92;
}

.address-list-metro__blue:before {
  background: #56bdef;
}

.address-list-metro__violet:before {
  background: #293694;
}

.address-more {
  margin: 0;
  padding: 0;
  list-style: none;
}

.address-more-item {
  padding: 0 0 0 0;
  margin: 0 0 1.500em 0;
  position: relative;
