.reviews {
  margin: 0;
  padding: 0;
  list-style: none;
}

.reviews-item:not(:last-of-type) {
  padding: 0 0 1.7em 0;
}

.reviews-item-signature {
  font-size: .9em;
  color: #818181;
}

.reviews-popup-wrap {
  text-align: left;
  padding: 1.250em;
}

.landing-container {
  padding: 1.5em 0 3.5em;
}

.landing-container:first-of-type {
  padding-top: 0;
}

.landing-container h2, .landing-container .header-2 {
  padding-bottom: 1.5em;
}

.landing-container .list-main {
  padding-bottom: 2em;
}

.photogallery ul {
  margin: 0;
  padding: 0 0 20px 0;
  list-style: none;
}

.photogallery ul li {
  width: calc((100% - (2 * 3px)) / 3);
  margin-left: 3px;
  box-sizing: border-box;
  display: inline-block;
}

.photogallery ul li:nth-child(3n+1) {
  margin-left: 0;
}

.photogallery ul li img {
  width: 100%;
}

.photogallery button {
  outline: none;
}

.course-items {
  margin: -1.3em -1.357em 0 -1.357em;
  padding: 0;
  list-style: none;
}

.course-items .item {
  border-bottom: 0.063em solid #dcdcdc;
  padding: 1em 0.7em 1em 1.357em;
}

.course-items .item a {
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  text-decoration: none;
  color: #818181;
}

.course-items .item a .item-title {
  display: block;
  text-decoration: underline;
  color: #960000;
  margin-bottom: 0.8em;
  font-family: "helveticaneuecyrmedium";
}

.course-items .item img {
  height: 0.75em;
  width: 0.75em;
}

/* Elements */
.button-green-large, .button-green-small, .button-blue-large, .button-white-large {
  display: inline-block;
  vertical-align: middle;
  background: #35a45b;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  line-height: 2.8em;
  text-decoration: none;
  font-family: 'helveticaneuecyrbold';
  height: 2.7em;
  padding: 0 0.625em;
  border-radius: 2.7em;
  border: 0;
  padding: 0 1.5em;
  position: relative;
}

.button-green-small {
  height: auto;
  font-family: 'helveticaneuecyrmedium';
}

.button-blue-large {
  background: #3a6699;
}

.button-white-large {
  background: transparent;
  color: #818181;
  font-family: 'helveticaneuecyrmedium';
  line-height: 2.6em;
  border: 1px solid #acacac;
  position: relative;
  text-transform: none;
}

.btn, .btn-fill, .btn-border, .btn-border-gray {
  display: inline-block;
  vertical-align: middle;
  color: black;
  font-size: 13px;
  text-decoration: none;
  font-family: "helveticaneuecyrmedium";
  padding: 7px 12px 8px;
  border-radius: 24px;
  border: 0;
  position: relative;
}

.btn:disabled, .btn-fill:disabled, .btn-border:disabled, .btn-border-gray:disabled {
  background: #818181;
  border-color: #818181;
}

.btn span, .btn-fill span, .btn-border span, .btn-border-gray span {
  line-height: inherit !important;
}

.btn-fill {
  background: #ae4037;
  border: 1px solid #ae4037;
  color: white;
}

.btn-fill img {
  height: 14px;
  margin-right: 5px;
  top: 3px;
  position: relative;
}

.btn-fill.btn-calc {
  padding-left: 9px;
}

.btn-border, .btn-border-gray {
  background: transparent;
  border: 1px solid #ae4037;
  color: #ae4037;
}

.btn-border-gray {
  border: 1px solid #8a8a8a;
  color: #8a8a8a;
}

.input-text,
.textarea,
.select {
  border: 1px solid #acacac;
  border-radius: 0.313em;
  font-family: "helveticaneuecyrregular";
  font-size: 1em;
  height: 2.7em;
  box-sizing: border-box;
  padding: 0 0 0 0.625em;
  vertical-align: middle;
  width: 100%;
}

.textarea {
  line-height: normal;
  padding-top: 0.625em;
  overflow: auto;
  height: 6.250em;
}

.form-row {
  padding: 0 0 1em 0;
  position: relative;
}

.select {
  background: white;
}

.select__arrow {
  position: relative;
  cursor: pointer;
  padding-right: 2em;
}

.select__arrow:before {
  content: '';
  position: absolute;
  right: .5em;
  top: .9em;
  width: 1.2em;
  height: 1.2em;
  background: transparent url(../img/svg/dropdown-gray.svg) no-repeat;
  background-size: 1.2em;
  box-sizing: border-box;
}

.select .selected-items {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}

.multiselect-popup {
  padding: 0 0 20px 0;
}

.multiselect-list {
  margin: 0;
  padding: 0 0 3em 0;
  list-style: none;
  cursor: pointer;
}

.multiselect-list-item {
  position: relative;
  text-align: left;
  padding: 0.8em 4em 0.8em 1.2em;
  border-bottom: 0.063em solid #dcdcdc;
}

.multiselect-list-item:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 1.1em;
  width: 1.750em;
  height: 1.375em;
  margin-top: -0.6875em;
  background: url("../img/icons/bullet.png") no-repeat;
  background-size: contain;
  opacity: 0.2;
}

.multiselect-list-item__active:after {
  opacity: 1;
}

.badge {
  background: #35a45b;
  color: #fff;
  text-align: center;
  font-size: 12px;
  font-family: 'helveticaneuecyrbold';
  display: inline-block;
  border-radius: 100%;
  padding: 0.47em;
  min-width: 1.15em;
}

.button-phone {
  background: #35a45b;
  border-radius: 100%;
  box-sizing: border-box;
  width: 2.9em;
  height: 2.9em;
  padding: 0.6em;
  display: inline-block;
}

.button-phone:after {
  content: "";
  display: block;
  background: url("../img/icons/phone.svg") no-repeat 0 0.063em;
  width: 100%;
  height: 100%;
}

